export default {
	props:{
		imglist:{
			type:Array
		}
	},
	data() {
		return {
			
		};
	},
	methods: {
		previewimgs(imagepath) {
			uni.previewImage({
				urls: this.imglist,
				current: imagepath,
				indicator: 'default',
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log(data.index + 1);
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				},
				success(res) {
					console.log(res);
				}
			})
		}
	},
}
